<template>
    <div class="page-container">
        <div class="submenu-title">관리자 정보변경</div>
        <div class="product-container">
            <form novalidate @submit.prevent="validate">
                <div class="product-regist">
                    <div class="product-form">
                        <table class="table-regist" cellspacing="0" cellpadding="0" border="0">
                            <tbody>
                            <tr>
                                <th><span class="required-icon">*</span>관리자명</th>
                                <td>
                                    <div class="input-box">
                                        <v-text-field v-model="name"
                                                      :error-messages="nameError"
                                                      dense
                                                      @input="$v.name.$touch()"
                                                      @blur="$v.name.$touch()"
                                                      placeholder="관리자명을 입력해주세요">

                                        </v-text-field>

                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <th><span class="required-icon">*</span>이메일</th>
                                <td>
                                    <div class="input-box">
                                        <v-text-field
                                                v-model="email"
                                                :error-messages="emailErrors"
                                                dense
                                                @input="$v.email.$touch()"
                                                @blur="$v.email.$touch()"
                                                placeholder="이메일을 입력해주세요"/>

                                    </div>
                                    <div style="margin:10px 0 5px 0;color:#880E4F">
                                        관리자 이메일은 메일 발송시 발송메일로 표시됩니다
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <th><span class="required-icon">*</span>현재 비밀번호</th>
                                <td class="flex">
                                    <div class="input-box">
                                        <v-text-field v-model="nowUpass"
                                                      type="password"
                                                      :error-messages="nowUpassError"
                                                      dense
                                                      @input="$v.nowUpass.$touch()"
                                                      @blur="$v.nowUpass.$touch()">
                                        </v-text-field>

                                    </div>
                                    <div class="tip">
                                        <v-btn class="white-button" style="width:110px;background: #000 !important;color:#fff;" depressed @click="updatePasswordToggle()" type="button">{{buttonTitle}}</v-btn>
                                    </div>
                                </td>

                            </tr>
                            <tr  v-if="updatePassword" >
                                <th><span class="required-icon"  v-if="updatePassword" >*</span>새 비밀번호</th>
                                <td class="flex">
                                    <div class="input-box">
                                        <v-text-field v-model="upass"
                                                      type="password"
                                                      :error-messages="upassError"
                                                      dense
                                                      @input="$v.upass.$touch()"
                                                      @blur="$v.upass.$touch()">
                                        </v-text-field>
                                    </div>
                                </td>
                            </tr>
                            <tr  v-if="updatePassword" >
                                <th><span class="required-icon"  v-if="updatePassword" >*</span>새 비밀번호 확인</th>
                                <td class="flex">
                                    <div class="input-box">
                                        <v-text-field v-model="reUpass"
                                                      type="password"
                                                      :error-messages="reUpassError"
                                                      dense
                                                      @input="$v.reUpass.$touch()"
                                                      @blur="$v.reUpass.$touch()">
                                        </v-text-field>
                                    </div>
                                </td>
                            </tr>

                            </tbody>
                        </table>
                    </div>
                    <div class="button-row">
                        <v-btn class="submit-button" type="submit">관리자 정보수정</v-btn>
                    </div>
                </div>
            </form>
        </div>
    </div>
</template>


<script>
import apiService from '@/config/admin/apiService'
import {
    API_ADMIN_UPDATE_ADMIN_INFO,
    API_ADMIN_GET_ADMIN_INFO
} from '@/config/admin/urls'
import {validationMixin} from 'vuelidate'
import {email, required} from 'vuelidate/lib/validators'

export default {
    mixins: [validationMixin],
    validations: {
        name: {required},
        email: {required, email},
        nowUpass: {required},
        upass: {required},
        reUpass: {required},

    },
    computed: {
        nameError() {
            const errors = []
            if (!this.$v.name.$dirty) return errors
            !this.$v.name.required && errors.push('관리자명을 입력하세요')
            return errors;
        },
        emailErrors() {
            const errors = []
            if (!this.$v.email.$dirty) return errors
            if (!this.$v.email.required) {
                errors.push('이메일을 입력하세요')
            }
            if (!this.$v.email.email) {
                errors.push('잘못된 형식의 이메일 입니다')
            }
            return errors;

        },
        nowUpassError() {
            const errors = []
            if (!this.$v.nowUpass.$dirty) return errors
            !this.$v.nowUpass.required && errors.push('현재비밀번호를 입력하세요')
            return errors;
        },
        upassError() {
            const errors = []
            if (!this.$v.upass.$dirty) return errors
            !this.$v.upass.required && errors.push('새비밀번호를 입력하세요')
            return errors;
        },
        reUpassError() {
            const errors = []
            if (!this.$v.reUpass.$dirty) return errors
            !this.$v.reUpass.required && errors.push('새비밀번호 확인을 입력하세요')
            if(this.reUpass && this.reUpass != this.upass) {
                errors.push('새비밀번호와 새비밀번호확인이 다릅니다')
            }
            return errors;
        },
    },
    data() {
        return {
            name: '',
            email:'',
            nowUpass:'',
            upass:'',
            reUpass:'',
            updatePassword:false,
            buttonTitle:'비밀번호변경'

        };
    },
    created() {
        this.getData();
    },
    methods: {
        getData() {
            apiService.post(API_ADMIN_GET_ADMIN_INFO, {}, (res) => {
                if (res.name) {
                    this.name = res.name;
                    this.email = res.email;
                }
            })
        },
        updatePasswordToggle() {
            if(this.updatePassword) {
                this.updatePassword = false;
                this.buttonTitle = '비밀번호변경';
            } else {
                this.updatePassword = true;
                this.buttonTitle = '변경취소';
            }
        },
        validate() {

            this.$v.$touch();
            let anyError = false;
            for(const f in this.$v.$params) {
                if(this.updatePassword) {
                    if(this.$v[f].$anyError) anyError = true;
                } else {
                    switch(f) {
                        case 'name':
                        case 'email':
                        case 'nowUpass':
                            if (this.$v[f].$anyError) anyError = true;
                            break;
                    }
                }

            }
            if(this.updatePassword) {
                if (this.reUpass != this.upass) {
                    return;
                }
            }
            if (!anyError) {
                let frm = {
                    name: this.name,
                    email:this.email,
                    nowUpass: this.nowUpass
                }
                if(this.updatePassword) {
                    frm.upass = this.upass;
                }
                if (this.serverFlag) return;
                this.serverFlag = true;
                console.log(frm)
                this.$eventBus.$emit('overlay', 'open');
                apiService.post(API_ADMIN_UPDATE_ADMIN_INFO, frm, (res) => {
                    this.serverFlag = false;
                    this.$eventBus.$emit('overlay', 'hide');
                    let msg = '';
                    if(res.status == 'message') {
                        msg = '잘못된 현재 비밀번호 입니다';
                    } else {
                        msg = '관리자정보가 수정 되었습니다';

                    }
                    let params = {
                        message: msg
                    }
                    this.$eventBus.$emit('modalOpen', params);
                })
            }
        },
    }

}
</script>

